/* Markdown */
.markdown-text {
  font-size: 14px;
}
.markdown-text h1,
  .markdown-text h2,
  .markdown-text h3,
  .markdown-text h4,
  .markdown-text h5,
  .markdown-text h6 {
    margin: 30px 0 15px;
}
.markdown-text p {
    line-height: 1.7em;
    overflow: auto;
    white-space: pre-wrap;
    word-wrap: break-word;
    margin: 15px 0;
}
.markdown-text a {
    color: #08c;
    text-decoration: none;
}
.markdown-text ol,
  .markdown-text ul {
    padding: 0;
    margin: 0 0 10px 25px;
}
.markdown-text li {
    line-height: 2em;
}
.markdown-text blockquote {
    padding: 0 0 0 15px;
    margin: 0 0 20px;
    border-left: 5px solid #eee;
}
.markdown-text img {
    height: auto;
    max-width: 100%;
}
.markdown-text > :first-child {
    margin-top: 0;
}
.markdown-text > :last-child {
    margin-bottom: 15px;
}

/* Transition */
.slide-enter-active,
.slide-leave-active {
  -webkit-transition: .3s ease;
  transition: .3s ease;
}
.slide-enter,
.slide-leave-to {
  -webkit-transform: translateX(-125px);
          transform: translateX(-125px);
  opacity: 0;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: .3s ease;
  transition: .3s ease;
}
.fade-enter,
.fade-leave-to {
  -webkit-transform: translateX(15px);
          transform: translateX(15px);
  opacity: 0;
}
.pop-enter-active,
.pop-leave-active {
  -webkit-transition: .3s ease;
  transition: .3s ease;
}
.pop-enter,
.pop-leave-to {
  -webkit-transform: translateY(125px);
          transform: translateY(125px);
  opacity: 0;
}
